.container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1rem;
  padding: 1rem;
}

.container p {
  justify-content: center;
  align-items: center;
  border-radius: 2rem;
  width: 99%;
  padding: 1rem;
}

.container h2 {
  border-bottom: 1px solid black;
  width: 100%;
  padding-bottom: 1rem;
  margin: 0;
  padding-top: 1rem;
  text-align: center;
  background-color: #ceb0b0;
  color: #fff;
}

.container button {
  color: #fff;
  padding: 0.5rem;
  justify-content: flex-end;
  background-color: #dc3545;
  border: none;
  border-radius: 5px;
}

.flex {
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  position: relative;
  background-color: #f5f5f5;
}

.container li {
  border-bottom: 1px solid black;
  margin-right: 10%;
  padding-bottom: 2rem;
  margin-bottom: 2rem;
  list-style: none;
}
